  --green-bg-two: #aec6a5;
  --brown-accent: #d6936d;
  --light-grey: #e1e1e1;
  --disabled-grey: #ababab;
  --green-hover-focus: #0a8817;
  --yellow-linear-gradient: linear-gradient(0deg, rgba(248, 210, 78, .7), rgba(248, 210, 78, .7)), #d9d9d9;
  --green-linear-gradient: linear-gradient(0deg, rgba(156, 190, 143, .7), rgba(156, 190, 143, .7)), #d9d9d9;
