.read-more-modal {
  background: #fcfcfc;
  border: 2px solid #f8d24e;
  border-radius: 10px;
  padding: 28px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  @media screen and (min-width: 468px) {
    width: 280px;
  }

  @media screen and (min-width: 768px) {
    width: 568px;
    padding: 32px 60px;
  }
}
.read-more-modal__close-btn {
  position: absolute;
  top: 12px;
  right: 12px;
  box-sizing: border-box;
  padding: 0;

  display: flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;

  border-radius: 50%;
  border-color: transparent;
  cursor: pointer;

  transition-property: fill;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transform: rotate(0deg);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

  .franchise-modal__icon-clse:hover & {
    fill: currentColor;
    transform: rotate(90deg);
  }
}

.read-more-modal__title {
  padding-bottom: 32px;

  font-family: var(--title-font);
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: calc(28 / 20);

  display: flex;
  align-items: center;
  text-align: center;
}
.read-more__video {
  padding-bottom: 20px;

  @media screen and (min-width: 768px) {
    padding-bottom: 32px;
    width: 448px;
    height: 205px;
  }
}

.read-more__video-ice-cream {
  width: 100%;
  height: 100%;
}
.read-more__text {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;

  color: #131313;
}
